.modal_form.active{
    display: block!important
}
.modal_form{
    display: none;
    position: fixed;
    background: rgb(0,0,0, 0.85);
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 10;

    .textarea_modal_form{

        textarea{
            resize: none;
            width: 100%;
            height: 190px;
            padding: 24px 20px 26px;
            background: #181818;
            font-weight: 400;
            font-size: 14px;
            color: white;
            border-radius: 0;
            line-height: 17px;
            font-family: 'TT Norms', sans-serif;
            border-radius: 0;
            border: 1px solid rgba(255, 255, 255, 0.6);
        }
        textarea:hover{
            border: 1px solid rgba(255, 255, 255, 0.8);
            border-radius: 0;outline: none;
        }
        textarea:focus{
            border: 1px solid rgba(255, 255, 255, 1);
            border-radius: 0;outline: none;
        }
    }

    .modal_form__wrap{
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        overflow-y: scroll;
    }

    .modal_form__wrap::-webkit-scrollbar{
        display: none
    }
    .content_modal_form{
        width: 1030px;
        padding: 26px 30px 50px;
        background: #181818;
        border: 1px solid rgba(255, 255, 255, 0.2);
        box-sizing: border-box;
        color: white;
        height: fit-content;
        position: relative;
    }
    .box_close_btn_modal_form{
        display: flex;
        justify-content: flex-end;
    }
    .close_modal_form{
        width: 80px;
        height: 80px;
        opacity: 0.2;
        border: 1px solid #FFFFFF;
        box-sizing: border-box;
        border-radius: 50%;
    }
    .title_modal_form{
        margin-bottom: 16px;
        font-weight: 400;
        font-size: 48px;
        line-height: 1;
    }
    .mini_text_modal_form{
        color: rgba(255, 255, 255, 0.5);
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
    }
    .text_modal_form{
        color: rgba(255, 255, 255, 0.5);
        font-weight: 400;
        font-size: 16px;
        padding-top: 10px;
        line-height: 150%;
    }
    .big_inputs_box_modal_form{
        width: 100%;
    }
    .inputs_box_modal_form{
        margin-top: 46px;
        width: 470px;
    }
    
    .input_icon_modal_form{
        position: relative;
        margin-bottom: 15px;

        .icon_modal_form{
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            width: 50px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        input{
            width: 100%;
            padding: 24px 50px 26px;
            background: #181818;
            font-weight: 400;
            font-size: 14px;
            color: white;
            line-height: 17px;
            font-family: 'TT Norms', sans-serif;
            border-radius: 0;
            border: 1px solid rgba(255, 255, 255, 0.6);
        }
        input:hover{
            border: 1px solid rgba(255, 255, 255, 0.8);
            border-radius: 0;outline: none;
        }
        input:focus{
            border: 1px solid rgba(255, 255, 255, 1);
            border-radius: 0;outline: none;
        }
    }
    .invisible {
        position: absolute;
        z-index: -1;
        width: 0;
        height: 0;
        opacity: 0;
    }
    .btn_modal_form{
        width: 310px;
        height: 70px;
        display: flex;
        background: white;
        color: #3c4ad2;
        font-weight: 500;
        border: 0px solid white;
        box-shadow: none;
        margin-top: 37px;
        font-size: 15px;
        line-height: 18px;
        align-items: center;
        justify-content: center;
    }

    .btn_close_form{
        width: 310px;
        height: 70px;
        display: flex;
        color: white;
        font-weight: 500;
        border: 1px solid #3c4ad2;
        box-shadow: none;
        background: rgb(0,0,0, 0);
        margin-top: 37px;
        font-size: 15px;
        line-height: 18px;
        align-items: center;
        justify-content: center;
    }

    .policy_modal_form{
        display: flex;
        align-items: center;
        font-weight: 400;
        margin-top: 15px;
        font-size: 14px;
        line-height: 17px;
        color: rgba(255, 255, 255, 0.5);

        a{
            text-decoration: underline;
        }
        a:hover{
            color: white
        }

        .label-cbx{
            display: flex;
            align-items: center;
        }
        .label-cbx input:checked + .checkbox {
            border-color: rgba(255, 255, 255, 0)
        }
        .label-cbx input:checked + .checkbox svg path {
            fill: white;
        }
        .label-cbx input:checked + .checkbox svg polyline {
            stroke-dashoffset: 0;
        }
        .label-cbx:hover .checkbox svg path {
            stroke-dashoffset: 0;
        }
        .label-cbx .checkbox {
            position: relative;
            top: 0px;
            float: left;
            margin-right: 13px;
            width: 19px;
            box-sizing: border-box;
            height: 19px;
            background-color: white;
        }
        .label-cbx .checkbox svg {
            position: absolute;
        }
        .label-cbx .checkbox svg polyline {
            fill: none;
            stroke: #3c4ad2;
            stroke-width: 2;
            stroke-linecap: round;
            stroke-linejoin: round;
            stroke-dasharray: 18px;
            stroke-dashoffset: 18px;
            transition: all 0.3s ease;
        }
        .label-cbx > span {
            pointer-events: none;
            vertical-align: middle;
        }
          
    }

    .big_inputs_box_modal_form{
        margin-top: 45px;
    }
    .box_title_photo_modal_form{
        position: absolute;
        right: 0;
        bottom: 0;

        .title_box_modal_form{
            font-weight: 400;
            font-size: 24px;
            line-height: 1;
            margin-bottom: 25px;
            max-width: 300px;
        }
        .photo_box_modal_form{
            width: 425px;
            height: 370px; 
            background-size: cover
        }
    }


    
}

@media(max-width: 1100px){
    .box_title_photo_modal_form{
        display: none
    }
    .modal_form{
        .content_modal_form{
            width: 650px;

            .inputs_box_modal_form{
                width: 100%;
            }
        }
    }
}
@media(max-width: 767px){
    .modal_form{
        .modal_form__wrap{
            
            .content_modal_form{
                padding: 25px 20px;
                width: calc(100% - 40px);
                margin: 65px auto 45px ;
                    
                .inputs_box_modal_form{
                    margin-top: 24px;
                }
                .input_icon_modal_form input{
                    padding: 17px 50px;
                }
                .btn_modal_form{
                    margin-top: 25px;
                    width: 100%;
                    height: 50px;
                }
                .btn_close_form{
                    margin-top: 25px;
                    width: 100%;
                    height: 50px;
                }
            }
            .close_modal_form {
                width: 60px;
                height: 60px;
            }
            .box_close_btn_modal_form{
                justify-content: center;
                margin-bottom: 28px;
            }

            .title_modal_form{
                font-weight: 400;
                font-size: 24px;
                line-height: 1;
            }
            
        }
    }
}
@media(max-height: 650px){
    .modal_form{
        .modal_form__wrap{
            align-items: unset;
            
        }
    }
    .textarea_modal_form{
        textarea{
            height: 150px;
        }
    }
    
}